/*General color Palette*/
:root {
  --color-primary: #2584ff;
  --color-secondary: #00d9ff;
  --color-accent: #ff3400;
  --color-headings: #1b0760;
  --color-body: #918ca4;
  --color-body-darker: #5c5577;
  --color-border: #ccc;
  --border-radius: 30px;
}

/*General sizing*/

*, 
*::before, 
*::after {
  box-sizing: border-box;
}


/*Typography*/

::selection {
  background-color: var(--color-primary);
  color: #fff;
}

html {
  font-size: 62.5%;
}

body { 
  font-family: Inter, Arial, Helvetica, sans-serif;
  color: var(--color-body);
  font-size: 2rem;
  line-height: 1.5;

}

h1, h2, h3 {
  color: var(--color-headings);
  margin-bottom: 1rem;
  line-height: 1.1;
}

h1 {
  font-size: 7rem;
}

h2 {
  font-size: 4rem;
}

h3 { 
  font-size: 3rem;
  font-weight: 500;
 
}

p {
  margin-top: 0;
}

@media screen and (min-width: 1024px) {
  
  body {
    font-size: 1.8rem;
  }
  
  h1 {
    font-size: 8rem;
  }
  
  h2 {
    font-size: 4rem;
  }
  
  h3 { 
    font-size: 2.4rem;

}
}

/*Links*/

a {
  text-decoration: none;
}

.link-arrow {
  color: var(--color-accent);
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: bold;
  
} 

.link-arrow::after {
  content: '-->';
  margin-left: 5px;
  transition: margin 0.15s;
}

.link-arrow:hover::after {
  margin-left: 10px;
}

@media screen and (min-width: 1024px) {
  .link-arrow {
    font-size: 1.5rem;
  }
  
}

/*Badges*/

.badge {
  border-radius: 20px;
  font-size: 2rem;
  font-weight: 600;
  padding: 0.5rem 2rem;
  white-space: nowrap;
}

.badge--primary {
  background-color: var(--color-primary);
  color: #fff;
}

.badge--secondary {
  background-color: var(--color-secondary);
  color: #fff;
}


.badge--small {
  font-size: 1.6rem;
  padding: 0.5rem 1.5rem;
  
}

@media screen and (min-width: 1024px) {
  .badge {
    font-size: 1.5rem;
  }

  .badge--small {
    font-size: 1.2rem;
  }
}

/*List*/

.list {
  list-style: none;
  padding-left: 0;
  
}

.list--inline .list__item {
  display: inline-block;
  margin-right: 2rem;
}

.list--tick {
  list-style-image: url(../images/tick.svg);
  padding-left: 3rem;
  color: var(--color-headings);
}

.list--tick .list__item {
  padding-left: 0.5rem;
  margin-bottom: 1rem;
}

@media screen and (min-width: 1024px) {
  .list--tick .list__item {
    padding-left: 0;
  }
  
}

/*ICons*/

.icon {
  width: 40px;
  height: 40px;
}

.icon--small {
  width: 30px;
  height: 30px;
}

.icon--primary {
  fill: var(--color-primary);
}

.icon--white {
  fill: #fff
}

.icon-container {
  width: 64px;
  height: 64px;
  border-radius: 100%;
  background-color: #f3f9fa;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.icon-container--accent {
  background-color: var(--color-accent);
}

/*Buttons*/

.btn {
  border-radius: 40px;
  border: 0;
  cursor: pointer;
  font-size: 1.8rem;
  font-weight: 600;
  margin: 1rem 0;
  outline: 0;
  padding: 2rem 4vw ;
  text-align: center;
  text-transform: uppercase;
  white-space: nowrap; 
}

.btn .icon {
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
  vertical-align: middle;
}

.btn--primary {
  background-color: var(--color-primary);
  color: #fff;
}

.btn--primary:hover {
  background-color: #388ffd ;
}

.btn--secondary {
  background-color: var(--color-secondary);
  color: #fff
}

.btn--secondary:hover {
  background-color: #00c8eb ;
}

.btn--accent {
  background-color: var(--color-accent);
  color: #fff
}

.btn--accent:hover {
  background-color: #fd4516 ;
}

.btn--block {
  display: inline-block;
  width: 100%;
  
}

.btn--outline {
  background-color: #fff;
  border: 2px solid var(--color-headings);
  color: var(--color-headings);
}

.btn--outline:hover {
  background-color: var(--color-headings); 
  color: #fff;
}

.btn--stretched {
  padding-left: 6rem;
  padding-right: 6rem;
}

@media screen and (min-width: 1024px) {
  .btn {
    font-size: 1.5rem;
  }
}

/*Input*/

.input {
  border-radius: var(--border-radius);
  border: 1px solid var(--color-border);
  color: var(--color-headings);
  font-size: 2rem;
  outline: 0;
  padding: 1rem 3.5rem;
}

::placeholder {
  color: #cdcbd7;
}

.input-group {
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  display: flex;
}

.input-group .btn {
  margin: 4px;
}


.input-group .input {
  border: 0;
  flex-grow: 1;
  padding: 1.5rem 2rem;
  width: 0;
}

@media screen and (min-width: 1024px) {
  .input {
    font-size: 1.5rem;
}
}

/*Cards*/

.card {
  border-radius: 7px;
  box-shadow: 0px 0px 20px 10px #f3f3f3;
  overflow: hidden;
}

.card__header,
.card__body {
  padding: 2rem 3rem;
}

.card--primary .card__header {
  background-color: var(--color-primary);
  color: #fff;
}

.card--secondary .card__header {
  background-color: var(--color-secondary);
  color: #fff;
}

.card--primary .badge--primary {
  background-color: #126de4;
}

.card--secondary .badge--secondary {
  background-color: #02cdf1;
}

/*Plans*/

.plan {
  transition: transform 0.2s ease-out;
}

.plan__name {
  color: #fff;
  margin: 0;
  font-weight: 500;
  font-size: 2.4rem;
}

.plan__price {
  font-size: 6rem;
}

.plan__billing-cycle {
  font-size: 2.4rem;
  font-weight: 300;
  opacity: 0.8;
  margin-right: 1rem;
}

.plan__description {
  font-size: 2rem;
  font-weight: 300;
  letter-spacing: 1px;
  display: block;
}

.plan .list__item {
  margin-bottom: 2rem;
}

.plan--popular {
  transform: scale(1.1)
}

.plan--popular .card__header {
  position: relative;
}

.plan--popular .card__header::after {
  content: url(../images/popular.svg);
  width: 40px;
  display: inline-block;
  position: absolute;
  top: -6px;
  right: 5%;

} 

.plan:hover {
  transform: scale(1.05);
}

.plan--popular:hover {
  transform: scale(1.15);
}
@media screen and (min-width: 1024px) {
  .plan__name {
    font-size: 1.4rem;
  }
  
  .plan__price {
    font-size: 5rem;
  }
  
  .plan__billing-cycle {
    font-size: 1.6rem;
    
  }
  
  .plan__description {
    font-size: 1.7rem;
  }
}

/*Media*/

.media {
  display: flex;
  margin-bottom: 4rem;
}

.media__title {
  margin-top: 0;
}

.media__body {
  margin: 0 2rem;
}

.media__image {
  margin-top: 1rem;
}

/*Quotes*/

.quote {
  font-size: 3rem;
  font-style: italic;
  color: var(--color-body-darker);
  line-height: 1.3;
}

.quote__text::before {
  content: open-quote;
}

.quote__text::after {
  content: close-quote;
}

.quote__author {
  font-style: normal;
  font-weight: 500;
  margin-bottom: 0;
  
}

.quote__organization {
  color: var(--color-headings);
  font-style: normal;
  font-size: 2rem;
  opacity: .4;
}

.quote__line {
  position: relative;
  bottom: 10px;
}

@media screen and (min-width: 1024px) {
  .quote {
    font-size: 2rem;
  }
  
  .quote__author {
    font-size: 2.4rem;
  }

  .quote__organization {
    font-size: 1.6rem;
  }
}

/*Grids*/

.grid {
  display: grid;
}

@media screen and (min-width: 768px) {
  .grid--1x2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1024px) {
  .grid--1x3 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

/*Testimonials*/

.testimonial {
  padding: 3rem;
}

.testimonial__image {
  position: relative;
}

.testimonial__image > img {
  width:100%;
}

.testimonial__image > .icon-container {
  position: absolute;
  top: 3rem;
  right: -32px;
}

@media screen and (min-width: 768px) {
  .testimonial .quote,
  .testimonial .quote__author {
    font-size: 2.4rem;
  }

  .testimonial .quote {
    margin-left: 6rem;
  }
}

/*Callout*/

.callout {
  padding: 4rem;
  border-radius: 5px;
}

.callout--primary {
  background-color: var(--color-primary);
  color: #fff;
}


.callout__heading {
  color: white;
  font-size: 3rem;
  margin-top: 0;
}

.callout .btn {
  justify-self: center;
  align-self: center;
}

.callout__content {
  text-align: center;
}

@media screen and (min-width: 768px) {
  .callout .grid--1x2 {
    grid-template-columns: 1fr auto;
  }

  .callout__content {
    text-align: left;
  }
  
  .callout .btn {
    justify-self: start;
    margin: 0 2rem;
  }
}

/*Collapsibles*/

.collapsible__header {
  display: flex;
  justify-content: space-between;

}

.collapsible__heading {
  margin-top: 0;
  font-size: 3rem;
}

.collapsible__chevron {
  transform: rotate(-90deg);
  transition: transform 0.3s;
}

.collapsible__content {
  max-height: 0;
  overflow: hidden;
  transition: all 0.3s;
  opacity: 0;
}

.collapsible--expanded .collapsible__chevron {
  transform: rotate(0);
}

.collapsible--expanded .collapsible__content {
  max-height: 100vh;
  opacity: 1;
}



/*Blocks*/

.block {
  --padding-vertical: 6rem;
  padding: var(--padding-vertical) 2rem;
}

.block__header {
  text-align: center;
  margin-bottom: 4rem;
}

.block__heading {
  margin-top: 0;
}

.block--dark {
  background-color: #000;
  color: #7b858b;
}

.block--dark h1,
.block--dark h2,
.block--dark h3
 {
  color: #fff;
}

.block--skewed-right {
  padding-bottom: calc(var(--padding-vertical) + 4rem);
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 90%);
}

.block--skewed-left {
  padding-bottom: calc(var(--padding-vertical) + 4rem);
  clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);
}

.container {
  max-width: 1140px;
  margin: 0 auto;
}


/*Navigation bars*/

.nav {
  background-color: #000;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 1rem;
  align-items: center;
}


.nav__list {
  width: 100%;
  margin: 0;
}

.nav__item {
  padding: 0.5rem 2rem;
  border: 1px solid #222;
}

.nav__item > a {
  color: #d2d0db;
  transition: color 0.3s;
  
}

.nav__item > a:hover {
  color: #fff;
}

.nav__toggler {
  opacity: 0.5;
  transition: box-shadow .15s;
  cursor: pointer;
}

.nav.collapsible--expanded .nav__toggler {
  opacity: 1;
  box-shadow: 0px 0px 0px 3px #666;
  border-radius: 5px;
}

.nav__brand {
  transform: translateY(5px)
}


@media screen and (min-width: 768px) {
  .nav__toggler {
    display: none;
  }

  .nav__list {
    width: auto;
    display: flex;
    font-size: 1.6rem;
    max-height: 100%;
    opacity: 1;
  }

  .nav__item {
    border: 0;
  }
}

/*Hero*/

.hero__tagline {
  font-size: 2rem;
  color: #b9c3c9;
  letter-spacing: 1px;
  margin: 2rem 0 5rem;
}

.hero__image {
  width: 100%;
}

@media screen and (min-width: 768px) {

  .hero {
    padding-top: 0;
  }

  .hero__content {
    text-align: left;
    align-self: center; 
  } 
}

/*Domain Block*/

.block-domain .input-group {
  box-shadow: 0px 0px 30px 20px #e6ebee;
  border: 0;
  max-width: 670px;
  margin: 4rem auto;

}


.block-domain__prices {
  color: var(--color-headings);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 6rem);
  font-size: 2rem;
  font-weight: 600;
  justify-items: center;
  max-width: 700px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .block-domain__prices {
    grid-template-columns: repeat(auto-fit, minmax(10rem,1fr))
  }
}


/*plan blocks*/

.block-plans .grid {
  gap: 8rem 4rem;
}


.block-plans .card {
  max-width: 500px;
  margin: 0 auto;
}


/*Features*/

.feature {
  gap: 4rem 2rem;
  margin: 12rem 0;
}

.feature:first-of-type {
  margin-top: 6rem;
}


.feature__heading {
  margin-top: 1rem 0;
}

.feature__image {
  width: 100%;
}

@media screen and (min-width: 768px) {
  .feature:nth-of-type(even) .feature__content {
    order: 2;
  }
  
}

/*Showcase Block*/

/* because we want the image to be responsive on mobile we have 
to give it its entire with.....But on laptop or wider screen we
we dont' want the image to be responsive that's why we are going 
to define a new width for it which is auto but also a max-with of
its definite size of 700px because we don't want it bi bigger thant it*/
.block-showcase__image > img {      
  width: 100%;
}

/*In our design the image is pushed to the left more which is outside of 
our content layout. and if we let it on auto it will take the entire 
place of its content in the grid which is 1fr (which will be determined 
by the width of the image and we don't want that), and the problem will be 
that the image will be bigger thant the other content as you can check it out.
That's why we will redifine the grid to 50% 50% instead of 1fr 1fr because
the grid is the container and it will mean 50% of the width of the container*/

@media screen and (min-width: 768px) {
  
  .block-showcase .grid {
    grid-template-columns: 50% 50%;
  }
  
  .block-showcase__image {
    justify-self: end;
    
  }
/*Here we apply a direct child element rule because we want to target
the image and because the image is inside another container we use this 
principle...we could also give the image element a class and target the
class but because we will not change this structure it is ok*/  
  .block-showcase__image > img {
    width: auto;
    max-width: 700px;
  }
 
}

/*Footer*/

.footer {
  background-color: #232323;
  padding-top: 10rem;
}

.footer a {
  color: #777;
}

.footer a:hover {
  color: #fff;
}

.footer__section {
  border-bottom: 1px solid #393939;
  padding: 2rem;
}

.footer__section .list {
  margin: 0;
}

.footer__heading {
  text-transform: uppercase;
  font-weight: 600;
}

.footer__brand {
  margin-top: 5rem;
  text-align: center;
}

.footer__copryright {
  font-size: 2.1rem;
  color: #fff;
  opacity: 0.3;
}

@media screen and (min-width: 768px) {
  .footer__sections {
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
  }

  .footer .collapsible__chevron {
    display: none;
  }

  .footer .collapsible__content {
    max-height: 100%;
    opacity: 1;
  }

  .footer__brand {
    order: -1;
  }

  /*TODO:consider refactoring this and apply it to all images*/

  .footer__brand img {
    width: 100%;
    max-width: 230px;
  }

  .footer__copryright {
    font-size: 1.5rem;
  }

  .footer__brand {
    margin-top: 1rem;
  }

  .footer__section {
    border: 0;
  }

  .footer__heading {
    font-size: 1.6rem;
  }
}

/*Callout signup*/

.callout--signup {
  transform: translateY(5rem);
}

